*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family:Verdana, Geneva, Tahoma, sans-serif;
}

body{
  background-color: black;
}

.navbar{
  color:white;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* box-shadow: 3px 3px 5px rgb(51, 51, 51); */
}


.navbar-items{
  width:400px;
  display: flex;
  justify-content: space-between;
  gap:20px;
  /* border:2px solid yellow; */
  /* margin-left: 700px; */
  margin-right: 100px;
  
}

.navbar-items button, .hmbg-menu button{
  font-size: large;
  background: none;
  border:none;
  color:yellow;
}

.navbar-items button:hover, .hmbg-menu button:hover{
  color:rgb(182, 12, 182);
}



.menu-lines-container{
  
  width:50px;
  height:50px;
  border:1px solid rgb(107, 171, 185);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap:3px;
  display: none;
}










.hmbg-menu{
  background-color: black;
  width:80vw;
  margin: auto;
  display: flex;
  flex-direction: column;
  
 
}


.hmbg-menu button{
  border:2px solid rgb(83, 82, 82);
}



.dynamic-name-display-container{
  /* border: 2px solid black; */
  width: 500px;
  margin:auto;
  margin-top:50px;
  text-align: center;
  
}

.dynamic-name-display-container h3{
  color:white;
}
.dynamic-name-display-container h1{
  color:yellow;
}


.main-content{
  width:800px;
  margin:auto;
  text-align: center;
  margin-top: 50px;
  /* box-shadow: 3px 3px 15px rgb(90, 88, 86); */
}
.main-content img{
  width:100%;
  height:400px;
}


.about-me{
  width:50vw;
  margin:auto;
  text-align: center;
  margin-top:60px;
  box-shadow: 3px 3px 15px rgb(90, 88, 86);
  background-color: black;
}
.about-me h1{
  color:white;
}
.about-me p{
  color:yellow;
  line-height: 1.7;
  word-wrap: break-word;
}



.skills-container{
  width:600px;
  margin:auto;
  color:white;
   height:300px;
  text-align: center;
  margin-bottom: 30px;
  margin-top: 100px;
  box-shadow: 3px 3px 15px rgb(90, 88, 86);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}


.carousel{
  height:70%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

 .carousel-item{
 
  display: flex;
  justify-content: center;
  align-items: center;
  width:80%;

 }



.left-arrow,.right-arrow{
  width:40px;
  height: 40px;
  border-radius: 40px;
  background-color: rgb(24, 24, 24);
  display: flex;
  justify-content: center;
  align-items: center;
}

.left-arrow:hover,.right-arrow:hover{
  background-color: rgb(43, 42, 42);
}




.footer{
  border-top: 2px solid rgb(44, 44, 44);
  width:100%;
  color: white;
  margin-top: 300px;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap:40px;
  justify-content: center;
  align-items: center;
}

.handles{
 display: flex;
 justify-content:center;
 align-items: center;
 gap:30px;
 margin-top: 10px;
}

.made-by{
  margin-bottom: 7px;
  width: 100%;
}




@media only screen  and (max-width:800px){
  .menu-lines-container{
    display: flex;
    margin-right:60px;
  }
  .navbar-items{
    display: none;
  }
  
  .dynamic-name-display-container{
     width:200px;
  }

.main-content{
  width:300px;
}
.main-content img{
  height:300px;
}

.hmbg-menu{
  width:100%;
  height:400px;
  border: 2px solid white;
}
.hmbg-menu button{
  height:30%;
  font-size: 1.4rem;
}

.skills-container{
  width:100%;
}

.footer{
  margin-top: 200px;
}
  
.about-me{
  width:90vw;
}
}
